input {
  margin: 0;
  padding: 0;
  border: 0;
  font-weight: normal;
  vertical-align: baseline;
}
a {
  color: #7e8c8d;
  text-decoration: none;
  -webkit-backface-visibility: hidden;
}
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
li {
  list-style: none;
}
::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}
::-webkit-scrollbar-track-piece {
  background-color: rgba(0, 0, 0, 0.2);
  -webkit-border-radius: 6px;
}
::-webkit-scrollbar-thumb:vertical {
  height: 5px;
  background-color: rgba(125, 125, 125, 0.7);
  -webkit-border-radius: 6px;
}
::-webkit-scrollbar-thumb:horizontal {
  width: 5px;
  background-color: rgba(125, 125, 125, 0.7);
  -webkit-border-radius: 6px;
}
input:focus,
button:focus {
  outline: none;
}
.clearfix {
  display: inline-block;
}
.clearfix::after {
  content: ".";
  display: block;
  height: 0;
  line-height: 0;
  visibility: hidden;
  clear: both;
}
@font-face {
  font-family: 'icomoon';
  src: url('assets/fonts/icomoon.eot?k0jdef');
  src: url('assets/fonts/icomoon.eot?k0jdef#iefix') format('embedded-opentype'), url('assets/fonts/icomoon.ttf?k0jdef') format('truetype'), url('assets/fonts/icomoon.woff?k0jdef') format('woff'), url('assets/fonts/icomoon.svg?k0jdef#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}
[class^="icon-"],
[class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.icon-back:before {
  content: "\e900";
}
.icon-password:before {
  content: "\e901";
}
.icon-user:before {
  content: "\e902";
}
.login-body {
  background: #dfdbe0;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.login-wrap {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 260px;
}
.login-row {
  position: relative;
  height: 35px;
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
  margin-bottom: 10px;
  font-size: 12px;
  color: rgba(255, 255, 255, 0.9);
}
.login-row i {
  position: absolute;
  left: 0;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.login-row input {
  height: 100%;
  width: 100%;
  background: transparent;
  border-bottom: 1px solid rgba(255, 255, 255, 0.4);
  text-indent: 30px;
  color: #FFF;
}
.login-row ::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 0.9);
}
.login-btn {
  height: 44px;
  width: 100%;
  border-radius: 44px;
  background: #f73668;
  border: 0;
  color: #FFF;
  cursor: pointer;
  margin: 30px auto;
  display: block;
  text-align: center;
}
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}



